
import { onMounted, computed, reactive, toRefs, h, watch, ref, defineComponent } from "vue";
import { useStore } from "@/store/index";

export default defineComponent({
    setup() {
        const store = useStore();

        const state = reactive({
            colorBy: computed(() => store.state.colorBy),
            renderState: computed(() => store.state.renderState)
        });

        return {
            ...toRefs(state),
        };
    },
    methods: {
        lowLabel(colorBy: string) {
            switch (colorBy) {
                case 'position':
                case 'categorical':
                case 'length':
                    return "0"
                case 'norm':
                    return "low"
                case 'punctuation':
                    return ".?!"
                default:
                    ""
            }
        },
        highLabel(colorBy: string) {
            switch (colorBy) {
                case 'position':
                case 'length':
                    return "1"
                case 'categorical':
                    return "4"
                case 'norm':
                    return "high"
                case 'punctuation':
                    return "abc"
                default:
                    ""
            }
        }
    },
})

